<script>
	import {  getNoticeds,getUserMes } from "../../axios/profile/profile.js";
	export default {
		name:"MyFans",
		data() {
			return {
				  myuid:1,
				  mutual:true,
				  maxPageNum:1,//数据库中最大的页数
				  page:1,//当前的页数
				  follows:[],//关注的人的数据
				  uid:1,//当前用户的id
				  loadSize:20//一次加载的最大数量
			};
		},
		props: {
		  list: {
		    type: Object,
		    default() {
		      return {};
		    },
		  },
		},
		 methods:{  
		     showToggle(){
		    	this.mutual= !this.mutual
		    			    },
		//获取用户uid
		getUid(){
				 var a = '';
				 uni.getStorage({
				 	key:'userInfo',
				 	success: function(res) {
				 		a = res.data;
						window.myuid=a.uid;
				 	}
				 });
		},
		//获取所关注的所有人
		getNoticeds() {
		   getNoticeds(this.uid, this.page, this.loadSize).then((res) => {
			this.maxPageNum = res.data.data.maxPageNum;
			for (const item of res.data.data.detail) {
				this.follows.push(item.userInfo);
				console.log(res.data.data.detail)
			}
			// console.log(this.follows)
		  });
		},
		},
		mounted() {
			  this.getUid();
			  console.log(myuid)
			  this.getNoticeds(myuid,1,20);
			  
		},
		};
</script>

